uctModal: false,\r\n        businessType: '1',\r\n        gameList: '',\r\n        childType: '',\r\n        department1: '',\r\n        department2: '',\r\n        productName: '',\r\n        productLevel: '',\r\n        gameType: '',\r\n        platformType: '',\r\n        developModel: '',\r\n        phase: '',\r\n        company: '',\r\n        studio: '',\r\n        release: '',\r\n        projectManagers: [],\r\n        maintainManagers: [],\r\n        operationalManagers: [],\r\n        marketManagers: [],\r\n        departments2: []\r\n    },\r\n    init = Object.assign({}, origin);\r\n\r\nexport default {\r\n    data () {\r\n        return origin\r\n    },\r\n    methods: {\r\n\r\n        // 模糊搜索项目负责人\r\n        addProjectManager (items, targetVM) {\r\n            if ($.inArray(items, this.projectManagers) === -1 && items) {\r\n                this.projectManagers.push(items) \r\n            }\r\n            \r\n            targetVM.reset()\r\n        },\r\n\r\n        // 模糊搜索运营负责人\r\n        addOperationalManager (items, targetVM) {\r\n            if ($.inArray(items, this.operationalManagers) === -1 && items) {\r\n                this.operationalManagers.push(items) \r\n            }\r\n            \r\n            targetVM.reset()\r\n        },\r\n\r\n        // 模糊搜索运维负责人\r\n        addMaintainManager (items, targetVM) {\r\n            if ($.inArray(items, this.maintainManagers) === -1 && items) {\r\n                this.maintainManagers.push(items) \r\n            }\r\n            \r\n            targetVM.reset()\r\n        },\r\n\r\n        // 模糊搜索市场负责人\r\n        addMarketManager (items, targetVM) {\r\n            if ($.inArray(items, this.marketManagers) === -1 && items) {\r\n                this.marketManagers.push(items) \r\n            }\r\n            \r\n            targetVM.reset()\r\n        },\r\n\r\n        // 删除负责人\r\n        deleteUser (index, name) {\r\n            this[name].splice(index, 1)\r\n        },\r\n\r\n        // 新增产品\r\n        saveFn () {\r\n            if (this.businessType === '1') {\r\n\r\n                if (this.gameList && this.department1 && this.productLevel && this.gameType && this.platformType\r\n                    && this.developModel && this.phase && this.maintainManagers.length && this.company && this.studio && this.release) {\r\n\r\n                    this.saveVaild()\r\n                    \r\n                } else {\r\n                    this.$dispatch('show-notify', '存在未填写的必填项，请检查')\r\n                }\r\n\r\n            } else if (this.businessType !== '1' && this.businessType) {\r\n\r\n                if (this.department1 && this.productLevel && this.maintainManagers.length && this.company) {\r\n\r\n                    this.saveVaild()\r\n                    \r\n                } else {\r\n                    this.$dispatch('show-notify', '存在未填写的必填项，请检查')\r\n                }\r\n            } else {\r\n                this.$dispatch('show-notify', '请选择业务类型')\r\n            }\r\n        },\r\n\r\n        // 执行保存\r\n        saveVaild () {\r\n            this.$http({\r\n                url: '/product/operate/',\r\n                method: 'POST',\r\n                data: this.$data\r\n            })\r\n            .then(response => {\r\n                if (response.data.code === 200) {\r\n                    this.creatProductModal = false\r\n                    this.$data = Object.assign({}, init)\r\n                    this.getBusinessSearch()\r\n\r\n                    this.$dispatch('refresh')\r\n                    this.$dispatch('show-success')\r\n                } else {\r\n                    this.$dispatch('show-error', response.data.msg)\r\n                }\r\n            })\r\n        }\r\n    },\r\n    components: {\r\n        modal,\r\n        vSelect,\r\n        typeahead\r\n    },\r\n    vuex: {\r\n        actions: {\r\n            getBusinessSearch\r\n        },\r\n        getters: {\r\n            businessTypes: productTypes,\r\n            phases,\r\n            gameTypes,\r\n            platformTypes,\r\n            developModels,\r\n            gameLists,\r\n            productLevels,\r\n            studios,\r\n            releases,\r\n            companys,\r\n            departments1\r\n        }\r\n    },\r\n    watch: {\r\n        'businessType' (newVal) {\r\n            if (newVal === '1') {\r\n                this.productName = ''\r\n            } else if (newVal !== '1' && newVal) {\r\n                this.gameList = ''\r\n                this.childType = ''\r\n                this.gameType = ''\r\n                this.platformType = ''\r\n                this.developModel = ''\r\n                this.phase = ''\r\n                this.studio = ''\r\n                this.release = ''\r\n                this.company = this.company || '1'\r\n            }\r\n        },\r\n        'department1' (newVal) {\r\n            this.department2 = ''\r\n\r\n            this.$http({\r\n                url: '/product/getDepartment/?id=' + newVal,\r\n                method: 'GET'\r\n            })\r\n            .then(response => {\r\n                this.departments2 = response.data.departments2\r\n            })\r\n        }\r\n    },\r\n    events: {\r\n        'showCreateProduct' () {\r\n            this.creatProductModal = true\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.selected-tag {\r\n    border: 1px solid #ccc;\r\n    border-radius: 4px;\r\n    height: 26px;\r\n    margin: 4px 1px 0 3px;\r\n    padding: 0 .25em;\r\n    float: left;\r\n    line-height: 1.7em;\r\n    color: #147688;\r\n    background-color: #d7f3f9;\r\n    border-color: #91ddec;\r\n    white-space: nowrap;\r\n}\r\n\r\n.selected-tag .close {\r\n    float: none;\r\n    margin-right: 0;\r\n    font-size: 20px;\r\n    -webkit-appearance: none;\r\n    padding: 0;\r\n    cursor: pointer;\r\n    background: 0 0;\r\n    border: 0;\r\n    font-weight: 700;\r\n    line-height: 1;\r\n    color: #000;\r\n    text-shadow: 0 1px 0 #fff